<template>
  <div id="tablet">
    <!-- -->
    <!-- -->
    <InflectionPointQueryBox @searchSrldId="searchSrldId" v-if="layerId === 'inflectionPoint' || layerId === ''"></InflectionPointQueryBox>
    <!-- -->
    <SignBoardQueryBox @searchSrldId="searchSrldId" v-else-if="layerId === 'inflectionBiaopai'"></SignBoardQueryBox>
    <!-- -->
    <TabletQueryBox @searchSrldId="searchSrldId" v-else-if="layerId === 'inflectionJiebei'"></TabletQueryBox>
    <div class="demo-split">
      <Split v-model="split1" min="200px" max="600px">
        <div slot="left" class="demo-split-pane">
          <Map  ref="map1" :pointData="pointData" :layerId="layerId" @changeredlineid="changeredlineid"></Map>
        </div>
        <div slot="right" class="demo-split-pane">
          <InflectionPointTab :obj="obj" :srld="srld"
            @ondbclickDingwei="ondbclickDingwei"
            @getPointData="getPointData"
            @mapSizeChange="mapSizeChange"
            @clickRedLineTable="clickRedLineTable"></InflectionPointTab>
        </div>
      </Split>
    </div>
  </div>
</template>

<script>
import Map from '../map/InflectionPoint_map.vue'
import InflectionPointTab from './InflectionPoint_tab.vue'
import InflectionPointQueryBox from './InflectionPoint_query'
import SignBoardQueryBox from './SignBoard_query.vue'
import TabletQueryBox from './Tablet_query'

export default {
  name: 'Tablet',
  data () {
    return {
      /*  */
      obj: '',
      pointData: [],
      layerId: '',
      lon: '',
      lat: '',
      spotTable: true,
      srld: null, // id
      split1: 0.6
    }
  },
  watch: {
    // layerId
    layerId (val) {
      this.obj = ''
    }
  },
  components: {
    Map,
    InflectionPointTab,
    // 
    InflectionPointQueryBox,
    // 
    SignBoardQueryBox,
    // 
    TabletQueryBox
  },
  mounted () {
  },
  methods: {
    /*  */
    ondbclickDingwei (lon, lat, id, layerId) {
      this.$refs.map1.ondbclickDingwei(lon, lat, id, layerId)
    },
    /*  */
    searchSrldId (obj) {
      this.obj = obj
    },
    /* id/ */
    getPointData (data, id) {
      this.pointData = data
      this.layerId = id
    },
    /*  */
    mapSizeChange (spotTable) {
      if (spotTable === true) {
        this.spotTable = true
      } else {
        this.spotTable = false
      }
    },
    /**
     * @Description:id
     * @author 
     * @date 2019/4/11
    */
    changeredlineid (id) {
      this.srld = id
    },
    /*  */
    clickRedLineTable (id) {
      this.$refs.map1.lightRedline(id)
    }
  }
}
</script>

<style lang='scss' scoped rel='stylesheet/scss'>
  #tablet{
    position: relative;
    .map_box{
      position:absolute;
      width:60%;
      top: 55px;
      bottom: 0;
    }
    .cascader_box{
      position: absolute;
      left:70px;
      top:70px;
    }
    .spotTableBox{
      position: absolute;
      right:0;
      top:55px;
      width:40%;
    }
    .demo-split{
      height: calc(100% - 65px);
      padding: 0 15px 15px;
    }
    .demo-split-pane{
      height: 100%;
    }
  }
</style>
